import {
  MissingPostalCodeError,
  InvalidPostalCodeError,
  InvalidAddressError,
  GenericShippingRatesError,
} from './service-plugins-types.js';

/**
 * Missing postal code error.

Throw this error when a postal code is not provided in the request.
This error tells Wix that a valid postal code is required to calculate the shipping rate.
You can optionally provide error data to help debug the issue.
Error data you provide appears in the monitoring log but is not exposed to the user.
This error may block the site visitor from proceeding to checkout, so throw it only if there is no other way to proceed.
 */
export class MissingPostalCodeWixError extends Error {
  /** @hidden */
  httpCode: number;
  /** @hidden */
  statusCode: string;
  /** @hidden */
  applicationCode: string;
  /** @hidden */
  name: string;
  /** @hidden */
  errorSchemaName: string;
  /** @hidden */
  errorType: string;
  /** @hidden */
  spiErrorData: object;

  data: MissingPostalCodeError;

  constructor(data: MissingPostalCodeError = {}) {
    super('MissingPostalCode');

    this.httpCode = 400;
    this.statusCode = 'INVALID_ARGUMENT';
    this.applicationCode = 'MISSING_POSTAL_CODE';
    this.name = 'MissingPostalCode';
    this.errorSchemaName =
      'com.wix.ecom.shipping_rates.v1.MissingPostalCodeError';
    this.errorType = 'SPI';
    this.spiErrorData = {
      name: 'MissingPostalCode',
      applicationCode: 'MISSING_POSTAL_CODE',
    };

    this.data = data;
  }

  /** @hidden */
  static readonly __type = 'wix_spi_error';
}

