/**
 * Invalid address error.

Throw this error when one or more fields in the address provided are invalid.
This error tells Wix that more address details are needed to calculate the shipping rate.
Wix may then prompt the user to provide more detailed address information.
The error data must include the invalid fields in the address. For example: `ShippingDestination.subdivision`.
 */
export class InvalidAddressWixError extends Error {
  /** @hidden */
  httpCode: number;
  /** @hidden */
  statusCode: string;
  /** @hidden */
  applicationCode: string;
  /** @hidden */
  name: string;
  /** @hidden */
  errorSchemaName: string;
  /** @hidden */
  errorType: string;
  /** @hidden */
  spiErrorData: object;

  data: InvalidAddressError;

  constructor(data: InvalidAddressError = {}) {
    super('InvalidAddress');

    this.httpCode = 400;
    this.statusCode = 'INVALID_ARGUMENT';
    this.applicationCode = 'PARTIAL_ADDRESS';
    this.name = 'InvalidAddress';
    this.errorSchemaName = 'com.wix.ecom.shipping_rates.v1.InvalidAddressError';
    this.errorType = 'SPI';
    this.spiErrorData = {
      name: 'InvalidAddress',
      applicationCode: 'PARTIAL_ADDRESS',
    };

    this.data = data;
  }

  /** @hidden */
  static readonly __type = 'wix_spi_error';
}

